<script>
import HeaderComponent from './components/HeaderComponent.vue';
import NavButton from './components/NavButton.vue';
import FooterComponent from './components/FooterComponent.vue';

import GreetingPage from './pages/GreetingPage.vue';
import AboutPage from './pages/AboutPage.vue';
import ProjectPage from './pages/ProjectPage.vue';
import ContactPage from './pages/ContactPage.vue';

export default {
    components: {
        HeaderComponent,
        NavButton,
        FooterComponent,
        GreetingPage,
        AboutPage,
        ProjectPage,
        ContactPage,
    },
    data() {
        return {
            activeSection: 'AboutPage',
            buttons: [
                { label: 'About', section: 'AboutPage' },
                { label: 'Projects', section: 'ProjectPage' },
                { label: 'Contacts', section: 'ContactPage' },
            ],
            activeButton: null
        };
    },
    methods: {
        activateSection(section) {
            this.activeSection = section;
        },
    },
};
</script>

<template>
    <div id="main">
        <HeaderComponent id="header" />
        <GreetingPage id="greeting" />
        <div class="info-pages">
            <div class="navbutton-container">
                <NavButton 
                    v-for="button in buttons" 
                    :key="button.label" 
                    :label="button.label" 
                    :section="button.section"
                    :is-active="button.section === activeSection" 
                    @click="activateSection(button.section)"
                    class="nav-btn"
                ></NavButton>
            </div>
            <div v-if="activeSection">
                <component class="info-page" :is="activeSection" />
            </div>
        </div>
    </div>
    <FooterComponent id="footer" />
</template>


